.contact_section{
  display: flex;
  min-height: 100vh;
  
  overflow: hidden;
  align-items: center;
  

  
}

input{
   box-shadow: 0 0 5px black;
}
.contact-outer_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin:auto;
    
    width: fit-content;
    height: fit-content;
    
    
}
.contact-outer_container span{
    color:#04edfe;
    font-size: 40px;
    font-weight: 700;
    
    
}
.contact-outer_container h1{
    color:white;
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 20px;
    opacity: 0;
    transform: translateY(-100px);
    transition: opacity 0.9s ease-in-out,transform 0.9s ease-in-out;
    
   
}
.contact-outer_container .active_contact_h1{
  opacity: 1;
  transform: translateY(0);

}
.contact-inner_container{
    display: flex;
    width:fit-content;
    row-gap: 5px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    
    height: 80%;
    opacity: 1;
    animation: opac 0.9s ease forwards;

}
.input-container{
    display:flex ;
    flex-direction: column;
    flex-wrap: wrap;
    width: fit-content;
    row-gap: 5px;
    justify-content: center;
    align-items: center;
    
}
input{
   
    width: 100%;
    background: #151820;
    opacity: 1;
    border-radius: 5px;
    padding-inline-start: 10px;
    
    height: 45px; /* or any other value you prefer */
}
.input-with-icon {
    display: flex;
    position: relative;
    width: 350px;
    
  }
  
.input-with-icon i.fa-check-circle,i.fa-times-circle  {
    position: absolute;
    right: 10px;
    top: calc(50% - 10px);
    bottom: 0;
    margin: auto;
    font-size: 20px;
    color: #04edfe;
  }
  
  .input-with-icon #textarea-icon{
    position: absolute;
    right: 14px;
    top:0;
    bottom: 0;
    margin: auto;
    font-size: 20px;
   
    color: #04edfe;
  }

  
.input-name{
  
  opacity: 0;
  transform: translateX(-100px);
  transition-property: opacity,transform;
  transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out ;
  transition-delay: 0.6s,0.6s;

}
.active_input-name{
  opacity: 1;
  transform: translateX(0);
  
}
.input-number{
  opacity: 0;
  transform: translateX(100px);
  transition-property: opacity,transform;
  transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out ;
  transition-delay: 0.6s,0.6s;
}
.active_input-number{
  opacity: 1;
  transform: translateX(0);

}
.input-email{
  opacity: 0;
  transform: translateX(-100px);
  transition-property: opacity,transform;
  transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out ;
  transition-delay: 0.8s,0.8s;
}
.active_input-email{
  opacity: 1;
  transform: translateX(0);
}
.input-email_subject{
  opacity: 0;
  transform: translateX(100px);
  transition-property: opacity,transform;
  transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out ;
  transition-delay: 0.8s,0.8s;
}
.active_input-email_subject{
  opacity: 1;
  transform: translateX(0);
  

}


.form-control:focus {
    outline: none !important;
    
    box-shadow: 0 0 2px 2px #04edfe;
  }
.form-group{
    display: flex;
    flex-wrap: wrap;
    width:fit-content;
    column-gap: 5px;
    row-gap: 5px;
    align-items: center;
    justify-content: center;
    
    
    
}
#text-areas{
    display: flex;
    width:100%;
    justify-content: center;
    align-items: center;
   
    
}
#text-areas textarea{
    
    width:100%;
    height: 250px;
   
    
    box-shadow: 0 0 5px black;
   
}
#text-areas .input-text{
  opacity: 0;
  transform: translateY(100px);
  transition-property: opacity,transform;
  transition: opacity 0.9s ease-in-out, transform 0.9s ease-in-out ;
  transition-delay: 1s,1s;
  


}
#text-areas .active_input-text{
  opacity: 1;
  transform: translateY(0);

}
#contact-send{
    margin-top: 10px;
  
   

}
.contact-btn{
  opacity: 0;
  scale: 0;
  transition-property: opacity,transform;
  transition: scale 0.9s ease-in-out, transform 0.9s ease-in-out ;
  transition-delay: 1.2s,1.2s;

}
.active_contact-btn{
  opacity: 1;
  scale: 1;
}
@keyframes rotateShake {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(-10deg);
    }
    20% {
      transform: rotate(10deg);
    }
    30% {
      transform: rotate(-10deg);
    }
    40% {
      transform: rotate(10deg);
    }
    50% {
      transform: rotate(-10deg);
    }
    60% {
      transform: rotate(10deg);
    }
    70% {
      transform: rotate(-10deg);
    }
    80% {
      transform: rotate(10deg);
    }
    90% {
      transform: rotate(-10deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  @keyframes rotateShake1 {
    0% {
      transform: rotate(0deg);
    }
    10% {
      transform: rotate(-1deg);
    }
    20% {
      transform: rotate(1deg);
    }
    30% {
      transform: rotate(-1deg);
    }
    40% {
      transform: rotate(1deg);
    }
    50% {
      transform: rotate(-1deg);
    }
    60% {
      transform: rotate(1deg);
    }
    70% {
      transform: rotate(-1deg);
    }
    80% {
      transform: rotate(1deg);
    }
    90% {
      transform: rotate(-1deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  
  .fa-shake {
    animation-name: rotateShake;
    animation-duration: 1s;
    animation-iteration-count: 1;
  }
  .fa-shake1 {
    animation-name: rotateShake1;
    animation-duration: 1s;
    animation-iteration-count: 1;
  }
  
@keyframes opac{
    0%{
     
      opacity: 1;
    }
    100%{
      
      opacity: 1;
    }
  

   }
   @media(max-width:992px){
    .form-group{
      padding-left: 5%;
      padding-right: 5%;
      width: 100%;
    }
    .contact-outer_container span{
      font-size: 35px;

    }
    .contact-outer_container h1{
      margin-bottom: 5px;
      font-size: 35px;
    }
    .contact-outer_container{
      position: relative;
      top: 55px;
    }
    #text-areas{
      margin-top: 5px;
      padding-left: 5%;
      padding-right: 5%;
      width:100%;
      
      
  }
    }