.skill_section{
    display: flex;
    min-height: 100vh;
    
    
    align-items: center;
    overflow: hidden;
    
  
    
  }

.outer-container {
    display: flex;

    height: fit-content;
    justify-content: space-evenly;
    align-items: center;

    width: 90%;

    margin: auto;
    margin-top: 50px;
    margin-bottom: 10px;
    flex-wrap: wrap;
    border-radius: 10px;



}


.container-skill1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    width: fit-content;
    height: fit-content;
   




}

#tech .skills {

    color: whitesmoke;

    font-size: 30px;
    font-weight: 700;
    margin-top: 30px;
    margin-bottom: 20px;
    opacity: 0;
    transform: translateX(-100px);
    transition: opacity 0.9s ease-in-out,transform 0.9s ease-in-out;



}
#tech .active_tech_skill_h3{
    opacity: 1;
    transform: translateX(0);

}

#prof .skills {

    color: whitesmoke;

    font-size: 30px;
    font-weight: 700;
    margin-top: 10px;
    margin-bottom: 20px;
    opacity: 0;
    transform: translateX(100px);
    transition: opacity 0.9s ease-in-out,transform 0.9s ease-in-out;



}

#prof .active_prof_skill_h3{
    opacity: 1;
    transform: translateX(0);

}


.skills span {

    color: #04edfe;


}

.container-3 {
    display: flex;
    flex-direction: column;
    width: 75%;
    height: 90%;
}

.my-skills {

    display: flex;
    overflow: hidden;
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-evenly;
    width: 500px;
    height: 350px;
    
    

    
    border-radius: 6px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 1);
    animation: opac 1s ease forwards;
    scale:0;
    transition-property: scale;
    
    transition:scale 0.9s ease-in-out;
    
   
    


}
.active_my_skills{
    opacity: 1;
    scale:1;
    


}


.my-skills:hover {
    outline: 2px solid #04edfe;
    box-shadow: 0 0 10px #04edfe;
    

   





}

.my-skill-inner{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-evenly;
}

.next-skills span,
.prev-skills span {
    display: inline-block;
    font-size: 50px;

    font-weight: 500;
    
    
    cursor: pointer;
    color: #04edfe;
    
    
}
.prev-skills{
    opacity: 0;
    transform: translateX(-100px);
    
    transition: 0.9s ease-in-out;
    transition-delay: 0.5s;
    
}
.active_prev-skill{
    opacity: 1;
    transform: translateX(0);

}


.next-skills{
    opacity: 0;
    transform: translateX(100px);
    
    transition: 0.9s ease-in-out;
    transition-delay: 0.5s;
}
.active_next-skill{
    opacity: 1;
    transform: translateX(0);

}

.next-skills span:hover,
.prev-skills span:hover {

    text-shadow: 0 0 20px #04edfe;
    scale:1.1;

}

.flip-skills {
    position: relative;
    display: flex;
    width: 100%;
    
    flex-direction: column;

}

.skill-box,
.skill-box1 {
    
    display: flex;
    
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin: 5px;
    transition: 0.3s ease-in-out;
  
    



}

.skill-box {
    transform: translateX(100%);
    opacity: 0;
    
    
}

.skill-box1 {
    transform: translateX(-100%);
    opacity: 0;
    
   
}

.active {
    
    transform: translateX(0);
    opacity: 1;
    transition-delay: 0.2s;
    

}
.page1-title{
    display: flex;
    width: fit-content;
    margin-bottom: 5px;
    align-items: center;
    justify-content: center;

}
.page1-title img{
    margin-left: 5px;
    width: 20px;
    height: 20px;
    opacity: 0;
    animation: zoomIn1 1s ease forwards;
    animation-delay: calc((0.2s*var(--p)))
}
.title {
    display: flex;
    cursor:default;
    font-size: 14px;
    font-weight: 600;
    color: white;


    opacity: 0;
    animation: slideRight 1s ease forwards;
    animation-delay: calc((0.1s*var(--p)));

}

.skill-bar {
    display: flex;
    height: 8px;
    width: 100%;


    border-radius: 6px;

    background: black;
}

.skill-per {
    position: relative;
}

.skill-per .tooltip {
    position: absolute;
    top: -40px;
    left: calc(100% - 13px);
    padding: 5px;

    color: white;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 900;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.skill-per:hover .tooltip {
    opacity: 1;
}

.tooltip::after {
    content: "";
    position: absolute;
    bottom: -4px;
    left: 20%;


    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #02d4e3;
}






.skill-bar .skill-per {

    display: flex;


    border-radius: 6px;
    background: #0ef;
    box-shadow: 0 0 100px rgba(0, 0, 0, 1), 0 0 3px rgba(0, 0, 0, 0.5) inset;
    opacity: 0;
    width: 0;
    transition: 0.9s ease-in-out;

}

.skill-per.html {
    opacity: 1;
    width: 75%;
    transition-delay: 0.6s;
    
}

.skill-bar .skill-per.python {
    opacity: 1;
    width: 95%;
    transition-delay: 0.7s;
    
}

.skill-bar .skill-per.sql {
    opacity: 1;
    width: 75%;
    transition-delay: 0.8s;
    
}
.skill-bar .skill-per.css {
    opacity: 1;
    width: 85%;
    transition-delay: 0.6s;
    
}

.skill-bar .skill-per.javascript {
    opacity: 1;
    width: 60%;
    transition-delay: 0.9s;
   
}
.skill-bar .skill-per.webscraping {
    opacity: 1;
    width: 60%;
    transition-delay: 0.5s; 
}

.skill-bar .skill-per.NLP {
    width: 60%;
    opacity: 1;
    animation-delay: 0.7s;
}

.skill-bar .skill-per.reactjs {
    opacity: 1;
    width: 70%;
    transition-delay: 0.6s;
}

.skill-bar .skill-per.expressjs {
    opacity: 1;
    width: 75%;
    transition-delay: 0.8s;
}

.container2 {
    display: flex;

    overflow: hidden;
    justify-content: space-evenly;
    align-items: center;

    width: 500px;
    height: 350px;
    
    background-color: #151820;
    border-radius: 6px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 1);
    animation: opac 1s ease forwards;
    scale:0;
    transition: scale 0.9s ease-in-out;




}
.active_container2{
    scale:1;
   
}



.container2:hover {
    outline: 2px solid #04edfe;
    box-shadow: 0 0 10px #04edfe;
    
}


.progress-filp {
    display: flex;

}

.progress-outer-container {
    display: flex;
    width: fit-content;
    flex-direction: column;
    justify-content: center;
    align-items: center;




}

.row-new-1 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 35%;
    height: 100%;
    
    flex-wrap: wrap;




}

.row-new-2 {
    display: flex;

    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
   
    width: 35%;
    height: 100%;




}


.progress-container .circular-progress {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 95px;
    width: 95px;
    border-radius: 50%;
    background: conic-gradient(#0ef 3.6deg, #121318 0deg);
    
    box-shadow: 0 0 50px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.5);
    transition: 1s ease-in-out;
}
.progress-container .communication{
    background: conic-gradient(#0ef calc(91 * 3.6deg), #121318 0deg);

}

.circular-progress:hover {
    animation: shadow 2s linear infinite;
    scale:1.1;



}

.progress-container {
    display: flex;
    backface-visibility: hidden;
    z-index: 1;
    opacity: 0;
    scale: 0;
    cursor:default;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: fit-content;
    transition: 0.3s cubic-bezier(0.24, 0.08, 0.66, 1.71);
    





}





.progress-value {
    position: relative;
    color: #fff;
}


.circular-progress::before {
    content: "";
    position: absolute;
    height: 85px;
    width: 85px;
    border-radius: 50%;
    background-color: var(--progress);

}


.skill-text-container1{
    display: flex;
    visibility: hidden;
    transform: translateX(-100px);
    opacity: 0;
    transition: 0.3s cubic-bezier(0.24, 0.08, 0.66, 1.71);
}
.skill-text-container2{
    display: flex;
    visibility: hidden;
    transform: translateX(100px);
    opacity: 0;
    transition: 0.3s cubic-bezier(0.24, 0.08, 0.66, 1.71);
}

.active1 {
    
    visibility: visible;
    scale: 1;
    transform: translateY(0);
    opacity: 1;
    
    transition-delay: 0.2s;
}
.text1,
.text2,
.text3,
.text4 {
    color: white;
    display: block;


    font-size: 15px;
    font-weight: 600;
    margin-top: 10px;
    opacity: 0;

}

.text1 {


    animation: slideRight 1s ease forwards;

    animation-delay: 1s;


}

.text2 {
    animation: slideLeft 1s ease forwards;
    animation-delay: 1s;

}

.text3 {


    animation: slideRight 1s ease forwards;

    animation-delay: 1s;

}

.text4 {



    animation: slideLeft 1s ease forwards;
    animation-delay: 1s;


}


@keyframes shadow{
    0%{
      
        box-shadow: 0 0 0px #0ef;
    }
    10%{
      
        box-shadow: 0 0 10px #0ef;
    }
    20%{
      
        box-shadow: 0 0 15px #0ef;
    }
    30%{
      
        box-shadow: 0 0 20px #0ef;
    }
    40%{
      
        box-shadow: 0 0 25px #0ef;
    }

    50%{
      
        box-shadow: 0 0 20px #0ef;
    }
    60%{
      
        box-shadow: 0 0 15px #0ef;
    }
    70%{
      
        box-shadow: 0 0 10px #0ef;
    }
    80%{
      
        box-shadow: 0 0 5px #0ef;
    }
    90%{
      
        box-shadow: 0 0 5px #0ef;
    }

    100%{
        box-shadow: 0 0 0px #0ef;
    }
}


@keyframes progress {
    0% {
        width: 0;
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}


@keyframes opac {
    0% {
        
        opacity: 0;
    }

    100% {
        
        opacity: 1;
    }
}

@keyframes slideLeft_arrow {
    0%{
      transform: translateX(40px);
      opacity: 0;
    }
    100%{
      transform: translateX(0);
      opacity: 1;
    }
    
   }
   @keyframes slideRight_arrow {
    0%{
      transform: translateX(-40px);
      opacity: 0;
    }
    100%{
      transform: translateX(0);
      opacity: 1;
    }
    
   }

@media(max-width:992px) {
    .next-skills span,
.prev-skills span {
   
    font-size: 45px;
    

  
}
.next-skills{
    margin-left:10px;
}

    .my-skills {
        width: 300px;
        height: 350px;
    }

    #tech .skills {
        font-size: 27px;
    }

    #prof .skills {
        font-size: 26px;
    }

    .container2 {
        width: 300px;
        height: 350px;
    }

    .outer-container {
        margin-top: 50px;
        margin-bottom: 20px;
    }

    .circular-progress::before {
        height: 85px;
        width: 85px;

    }

    .circular-progress {
        height: 85px;
        width: 85px;

    }

    .text1,
    .text2,
    .text3,
    .text4 {
        font-size: 13px;
    }
}