.about_section{
    display: flex;
    min-height: 100vh;
    max-height: fit-content;
    
    
    align-items: center;
    
  
    
  }

.about-outer-container{
    display: flex;
    width: 100%;
    margin: auto;
    justify-content: center;
    align-items: center;
    
    
}

.About-container{
    display: flex;
    height: fit-content;
    width: 100%;
    border-radius: 30px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 20px;
    
    
    
}
.about-image-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red;
}

.about-image{
    display:flex;
    width: 400px;
    height: 430px;
    justify-content: center;
    align-items: center;
    
    
    filter:drop-shadow(0px 20px 10px #0fe) drop-shadow(0 0 10px black);
    
    
    opacity: 0;
    scale: 0;
    
    transition:scale 0.9s ease-in-out,opacity 0.9s ease-in-out;
   
}
.active_about-image{
    
    scale: 1;
    opacity: 1;
}


.About-content{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    height:fit-content;
    
    margin-top: 70px;
    width:50%;
   
    
    
}
.About-content h4{
    color:white;
    font-size: 35px;
    font-weight: 700;
    opacity: 0;
    transform: translateY(-100px);
    transition: transform 0.9s ease-in-out, opacity 0.9s ease-in-out;
}
.About-content .active_h4{
    opacity: 1;
    transform: translateY(0);
    
}
.About-content h3{
    color:white;
    font-size: 25px;
    font-weight: 600;
    margin-bottom: 15px;
    opacity: 0;
    transform: translateX(100px);
    transition: transform 0.9s ease-in-out, opacity 0.9s ease-in-out;

}
.About-content .active_h3{
    opacity: 1;
    transform: translateX(0);


}

.About-content h4 span{
  color: #0ef;
  
}
.about-para{
    display: flex;
    width: 100%;
    flex:1 0 100px;
    height: fit-content;
  
    
    
    
    
    
}


.para{
    color:white;
    line-height: 1.6;
    opacity: 0;
    transform: translateY(100px);
    transition: transform 0.9s ease-in-out, opacity 0.9s ease-in-out;
    
    
}
.active_para{
    opacity: 1;
    transform: translateY(0);

}
.popup_para{
    color:white;
    line-height: 1.6;
    margin-right: 10px;
    
}


.About-content .btn{
   
    margin-top: 5px;
    opacity: 0;
    scale: 0;
    transition: scale 0.9s ease-in-out, opacity 0.9s ease-in-out;
    transition-delay: 0.5s;
    
}
.About-content .active_btn{
    opacity: 1;
    scale: 1;

    
}



.popup_about{
    display: flex;
    padding-bottom: 20px;
    
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position:absolute;
    border: 2px solid #04edfe;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 1),0 0 15px #04edfe;
    margin-top:60px;
    

    width: 50%;
    height: 80%;
    background-color: rgba(4, 4, 5, 0.8);
    transform: scale(0);
    
    transition: 0.5s cubic-bezier(.24,.08,.49,1.32);
}

.popup_about h3{
    color: white;
    font-size: 30px;
    font-weight: 700;
}
.popup_about h3 span{
    color: #0ef;
    
}
.popup_about .popup_about_content{
    display: flex;
    padding: 5px;
    margin-top: 10px;
    overflow: auto;
    text-align: justify;
    width:85%;
    height: 80%;
}
.popup_about .popup_about_content::-webkit-scrollbar{
    width: 8px;

    
}
.popup_about .popup_about_content::-webkit-scrollbar-thumb{
    background-color: #0ef;
    border-radius: 20px;
    
}
.popup_about i{
    position: absolute;
    top:10px;
    right: 10px;
    font-size: 20px;
    cursor: pointer;
    color: #0ef;
    
}

.popup_about .para{
    line-height: 1.7;
    font-weight: 500;
}
.popup_about_active{
    transform:scale(1)
}

@keyframes opac{
    0%{
     
      opacity: 1;
    }
    100%{
      
      opacity: 1;
    }
  
   }
   @media(max-width:992px){
    
    .about-image{
        width: 100%;
      height: 400px;
    

    }
    .about-outer-container{
        margin-top: 10%;
    }
    .About-content .btn{
        margin-top: 15px;
    }
    .About-content{
        width:100%;
        padding: 20px;
        
    }
    
    .popup_about{
        width: 95%;
    }
    .About-container{
        margin-top: 0;
    }
    }
