
#types{
    color: #07e4f4; 
}

#my-skill-list{
  padding-bottom: 10px;
}

.home_section{
  display: flex;
  min-height: 100vh;
  
  
  align-items: center;

  
}
  
.home-button{
  display: flex;
  justify-content: center;
  align-items: center;
}

#cont3{
    display:flex;
    width: 100%;
    height: fit-content;
    
    flex-wrap: wrap-reverse;
   
    border-radius: 20px;
    justify-content: space-evenly;
  align-items: center;
  margin-top: 15px;
  
  
 
 
  }


#cont1{
    display: flex;
    flex-direction: column;
    width: 630px;
    height: fit-content;
    align-items: flex-start;
    justify-content: space-between;
  color: white;
  padding: 25px;
  
  
 
 

  
  
  
  
}
#my-skill-list span Typed{
  transition: 1s ease;
}


#cont3 img{
   
  display:flex;
  width: 420px;
  height: 455px;
  justify-content: center;
  align-items: center;
  
  filter:drop-shadow(0px 20px 10px #0fe) drop-shadow(0 0 10px black);
  
   margin-left: -10px;
    opacity: 0;
  
    animation: zoomIn1 1s ease forwards,floatImage 4s ease-in-out infinite;
    animation-delay: 1s,2s;
    transition: 0.5s ease-in-out;
    
} 


.logos{
    
    display: flex;
    justify-content: space-between;
    width: fit-content;
   
    padding-bottom:17px ;


}
.content-main{
  display: flex;
  flex-direction: column;
  
  
    padding-bottom:5px ;
    
    
}
.content-main h3{
    font-size: 31px;
    font-weight: 500;
    
    opacity: 0;
    animation: slideBottom 1s ease forwards;
    
}
.content-main h1{
    font-size: 46px;
    font-weight: 500;
    
    opacity: 0;
    animation: slideRight 1s ease forwards;

    
    
    
}

.content-main h3:nth-of-type(2){
    animation: slideTop 1s ease forwards;

}
.content-main p{
    opacity: 0;
    animation: slideLeft 1s ease forwards;
}
.logos a{
  
    display: inline-flex;
    justify-content: center;

    align-items: center;
    margin: 5px;
    width: 33px;
    height: 33px;
    background:transparent ;
    border: 2px solid #07e4f4;
    border-radius: 50%;
    font-size: 20px;
    color:#04edfe;
    background: var(--modeback);
    text-decoration: none;
    transition:0.5s ease;
    opacity: 0;
    animation: slideLeft 1s ease forwards;
    animation-delay: calc(.1s*var(--i));
   
}

.logos a:hover{
    background:#04edfe;
    color: #1f242d;
}


#button1{
 
  opacity: 0;
    animation: slideTop 1s ease forwards;
    animation-delay:1s;
}
@keyframes slideTop{
    0%{
      transform: translateY(130px);
      opacity: 0;
    }
    100%{
      transform: translateY(0);
      opacity: 1;
    }
  
   }
@keyframes slideBottom{
    0%{
      transform: translateY(-130px);
      opacity: 0;
    }
    100%{
      transform: translateY(0);
      opacity: 1;
    }
  
   }
 @keyframes slideRight {
    0%{
      transform: translateX(-140px);
      opacity: 0;
    }
    100%{
      transform: translateX(0);
      opacity: 1;
    }
    
   }
   @keyframes slideLeft {
    0%{
      transform: translateX(140px);
      opacity: 0;
    }
    100%{
      transform: translateX(0);
      opacity: 1;
    }
    
   }
   @keyframes zoomIn {
    0%{
      transform:scaleX(-1);
      opacity: 0;
    }
    100%{
      transform: scaleX(1);
      opacity: 1;
    }
    
   }
   @keyframes floatImage{
    
    0%{
     
      transform: translateY(0);
      
      
    }
    50%{
        
        transform: translateY(-24px);
        
        
      }
    100%{
        
      transform: translateY(0);
      
      
    }
  
   }
   @keyframes zoomIn1{
    0%{
      transform:scale(0);
      opacity: 0;
    }
    100%{
      transform: scaleX(1);
      opacity: 1;
    }
    
   }

   @media(max-width:992px){
    .logos{
      padding-bottom:20px
    }
    #cont3{
      margin-top: 15%;
      
    }
    #cont3 img{
      width: 100%;
      height: 400px;
      margin-bottom: 15px;
      margin-left: 0
    }
    .content-main h1{
      font-size: 35px;
    }
    .content-main h3{
      font-size: 22px;
    }
    .content-main{
      width: 100%;
    }
    #cont1{
      width: 100%;
      margin: 0px;

    }
    .btn{
      margin-bottom: 100px;
    }
    }